import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom';
import Home from './taobao/Home';
import Collect from './taobao/Collect';
import Order from './taobao/Order';
import My from './taobao/My';
import Search from './taobao/Search';
import Detail from './taobao/Detail';
import { Provider } from 'react-redux';
import store from './redux/store';
import Login from './taobao/Login';


const Taobao = ()=>{
	return <Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/React_task/task5/' component={Home} />
				<Route path='/React_task/task5/collect' component={Collect} />
				<Route path='/React_task/task5/order' component={Order} />
				<Route path='/React_task/task5/my' component={My} />
				<Route path='/React_task/task5/search' component={Search}/>
				<Route path='/React_task/task5/detail' component={Detail}/>
				<Route path='/React_task/task5/login' component={Login}/>
			</Switch>
		</Router>
	</Provider>
	
}
export default Taobao;