let shoucang=[];

function collectionreducer(state=shoucang,action){
    if(action.type=='ADD_COLLECT'){
        return [...state,action.value]
    }
    if(action.type=='DEL_COLLECT'){
        let arr = [...state];
        arr.splice(action.value,1);
        return arr;
    }
    return state;
}
export default collectionreducer;