import React from 'react'
import { BrowserRouter as Router} from 'react-router-dom';
import { Button, Carousel, WingBlank } from 'antd-mobile';
import Mytab from './MyTab';
import GridExp from './GridExp';

class TB_Carousel extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['1/38/O1CN0152Uwo61C9RgI6tGUQ_!!38', '3/75/O1CN01HkIrIa1CQOKt160Ju_!!75', '4/67/O1CN01yPXGic1CMjAP1OVdX_!!67'],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank style={{width:'100%',margin:0}}>
        <Carousel style={{
          overflow: 'hidden',
        }}
          frameOverflow="hidden"
          autoplay
          infinite
          afterChange={index => this.setState({ slideIndex: index })}
        >
          {this.state.data.map((val, index) => (
            <a
              key={val}
              style={{
                display: 'block',
                position: 'relative',
                height: this.state.imgHeight,
              }}
            >
              <img
                src={`http://gw.alicdn.com/imgextra/i${val}-0-lubanu.jpg`}
                style={{ width: '100%', verticalAlign: 'top' ,height:'150px'}}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

const Home = (props) => {
    return <div>
    <Router>
      <div style={{
        height:'40px',
        backgroundImage:'url(data:image/png;base64,)',
        backgroundRepeat:'no-repeat',
        backgroundSize:'27px 20px',
        backgroundPosition:'9px center',
        backgroundColor:'#ff8c00'      
      }}>
        <Button style={{
          backgroundColor:'#ff4500',
          width:'85%',
          height:'24px',
          color:'white',
          fontSize:'13px',
          marginLeft:'45px',
          position:'relative',
          top:'8px',
          }}
          onClick={()=>{props.history.push('/React_task/task5/search')}}
        >
          <span className='iconfont icon-search' style={{
            fontSize: '15px',
            display: 'inline-block',
            color: 'rgb(255, 255, 255)',
            position:'relative',
            top:'-12px'
          }}>寻找宝贝店铺</span>
        </Button>
      </div>
      <TB_Carousel/>
    </Router>
    <GridExp />
    <Mytab />
    </div>
}

export default Home