import React from 'react'
import { connect } from 'react-redux'
import { Button } from 'antd-mobile'
import Mytab from './MyTab'

const Collect = (props) => {
    if(!(props.username || localStorage.getItem('username'))){
        return <div style={{textAlign:'center'}}>
                    <h3>请登录</h3>
                    <Button onClick={()=>{props.history.push('/React_task/task5/login')}}>去登陆</Button>
                    <h3>跳转回来后请耐心等待</h3>
            </div>
    }
    const title = (img) => {
        let tit = '';
        props.data.map((item)=>{
            if(item.img===img) tit=item.title;
        })
        return tit
    }
    return <div>
        <h2>我的收藏</h2>
        {
            props.shoucang.map((item,idx)=>(
                <li key={idx} style={{
                    listStyle:'none',
                    border:'1px solid',
                    marginBottom:'10%',
                }}>
                    <img src={item} style={{
                        width:'40%',
                        height:'40%',
                    }}/>
                    <p style={{
                        float:'right',
                        marginRight:'20%',
                        marginTop:'10%'
                    }}>{title(item)}</p>
                    <button 
                        style={{
                            display:'inline-block',
                            marginLeft:'100px',
                            height:'30px',
                            width:'60px',
                            float:'right',
                            marginTop:'-15%',
                            marginRight:'20%'
                        }}
                        onClick={()=>{
                            props.dispatch({
                                type: "DEL_COLLECT",
                                value:idx
                            })
                        }}
                    >删除</button>
                </li>
            ))
        }
        <Mytab />
    </div>
    
}
const mapStateToProps = (state) => ({
    username: state.login.username,
    data:state.goods.data,
    shoucang: state.collections
})
export default connect(mapStateToProps)(Collect);