import React from 'react';
import { Grid } from 'antd-mobile';
import {BrowserRouter as Router,withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {goods} from '../redux/actionCreators'


const GridExp = (props) => {
  (function(){
    props.dispatch(goods())
  })()
  const data = Array.from(props.data).map((item) => ({
    img:`${item.img}`,
    title:`${item.title}`,
    price:`${item.price}`
  }));
  
    return  <div style={{marginBottom:'50px'}}>
     <Grid data={data}
      columnNum={2}
      hasLine={false}
      square={false}
      itemStyle={{
        backgroundColor:'rgba(0,0,0,.05)',
        overflow:'hidden',
        width:'50%',
      }}
      renderItem={dataItem => (
        <div style={{
            marginLeft:'5%',
            width:'90%',
            height:'250px',
            backgroundColor:'white',
            borderRadius:'10px',
            overflow:'hidden',
        }}
        onClick={()=>{
          localStorage.setItem(
            'detail',
            `${dataItem.img}`
          )
          props.history.push('/React_task/task5/detail')
        }}
        >
          <img src={dataItem.img} style={{ width: '100%', height: '180px'}} alt="" />
          <div style={{ color: '#000', fontSize: '15px'}}>
            <span>
                {dataItem.title}
                <div style={{color:'#888', fontSize:'13px',marginTop:'15px'}}><span style={{color:'red',fontSize: '15px'}}>¥ {dataItem.price}</span>  15人已购买</div>
            </span>
          </div>
        </div>
      )}
    />
    </div>
};

const mapStateToProps = (state) => ({
  data:state.goods.data
})
export default connect(mapStateToProps)(withRouter(GridExp));

