import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="grey"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={ <div className='iconfont icon-Homehomepagemenu' />}
                        selectedIcon={ <div className='iconfont icon-Homehomepagemenu1'/>}
                        selected={ pathname === '/React_task/task5/' }
                        onPress={ () => {
                            this.props.history.push('/React_task/task5/');
                        }}
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={<div  className='iconfont icon-shoucang'/>}
                        selectedIcon={<div className='iconfont icon-collection-b'/>}
                        title="收藏夹"
                        key="collections"
                        selected={ pathname === '/React_task/task5/collect' }
                        onPress={ () => {
                            this.props.history.push('/React_task/task5/collect')
                        }}
                    ></TabBar.Item>
                    <TabBar.Item
                        title="订单列表"
                        key="order"
                        icon={ <div className='iconfont icon-dingdan' />}
                        selectedIcon={ <div className='iconfont icon-dingdan1'/>}
                        selected={ pathname === '/React_task/task5/order' }
                        onPress={ () => {
                            this.props.history.push('/React_task/task5/order');
                        }}
                    ></TabBar.Item>
                    <TabBar.Item
                        title="我的淘宝"
                        key="my"
                        icon={ <div className='iconfont icon-wode' />}
                        selectedIcon={ <div className='iconfont icon-wodedangxuan'/>}
                        selected={ pathname === '/React_task/task5/my' }
                        onPress={ () => {
                            this.props.history.push('/React_task/task5/my');
                        }}
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);